import { NextRequest, NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import { auth } from "@/lib/auth";
import { getUserRoleInProject, hasPermission, PERMISSIONS } from "@/lib/permissions";

// ... GET handler remains unchanged ...

// PUT /api/projects/[projectId]/tasks/[taskId]
export async function PUT(
  req: NextRequest,
  { params }: { params: Promise<{ projectId: string; taskId: string }> }
) {
  try {
    const session = await auth();
    if (!session?.user?.id) {
      return NextResponse.json({ message: "Unauthorized" }, { status: 401 });
    }

    const { projectId, taskId } = await params;
    const {
      title,
      description,
      columnId,
      order,
      parentId,
      assignees,
      startDate,
      endDate,
      toleranceDate,
      links,
      attachments,
      images,
      tags,
      priority,
      checklist
    } = await req.json();

    // First, fetch the current task
    const currentTask = await prisma.task.findUnique({
      where: { id: taskId, projectId },
      include: {
        children: true,
        assignees: true
      },
    });

    if (!currentTask) {
      return NextResponse.json({ message: "Task not found" }, { status: 404 });
    }

    // Check permissions
    const userRole = await getUserRoleInProject(session.user.id, projectId);
    const isOwnTask = currentTask.assignees.some(a => a.id === session.user.id);

    const canEditAny = hasPermission(userRole, PERMISSIONS.EDIT_ANY_TASK);
    const canEditOwn = hasPermission(userRole, PERMISSIONS.EDIT_OWN_TASK) && isOwnTask;

    if (!canEditAny && !canEditOwn) {
      // If trying to assign users, check ASSIGN_TASK permission specifically
      if (assignees && !hasPermission(userRole, PERMISSIONS.ASSIGN_TASK)) {
        return NextResponse.json({ message: "Forbidden: Cannot assign users" }, { status: 403 });
      }
      // If not assigning, just general edit check
      if (!assignees) {
        return NextResponse.json({ message: "Forbidden" }, { status: 403 });
      }
    }

    // If user only has EDIT_OWN_TASK, prevent them from changing critical fields like columnId (moving task) if not allowed?
    // For now, we'll assume EDIT_OWN_TASK allows moving.
    // But maybe we should restrict moving tasks for Artists? 
    // Let's stick to the plan: Artist can "Cambiar estado de sus tareas" (Change status = move column).



    // Check if columnId is changing and if this task has children
    const isColumnChanging = columnId !== undefined && columnId !== currentTask.columnId;
    const hasChildren = currentTask.children && currentTask.children.length > 0;

    // Use a transaction to update the task and its children atomically
    const updatedTask = await prisma.$transaction(async (tx) => {
      // Update the main task
      const updated = await tx.task.update({
        where: { id: taskId, projectId },
        data: {
          title: title !== undefined ? title : undefined,
          description: description !== undefined ? description : undefined,
          columnId: columnId !== undefined ? columnId : undefined,
          order: order !== undefined ? order : undefined,
          parentId: parentId !== undefined ? parentId : undefined,
          startDate: startDate !== undefined ? (startDate ? new Date(startDate) : null) : undefined,
          endDate: endDate !== undefined ? (endDate ? new Date(endDate) : null) : undefined,
          toleranceDate: toleranceDate !== undefined ? (toleranceDate ? new Date(toleranceDate) : null) : undefined,
          links: links !== undefined ? JSON.stringify(links) : undefined,
          attachments: attachments !== undefined ? JSON.stringify(attachments) : undefined,
          images: images !== undefined ? JSON.stringify(images) : undefined,
          tags: tags !== undefined ? JSON.stringify(tags) : undefined,
          priority: priority !== undefined ? priority : undefined,
          checklist: checklist !== undefined ? JSON.stringify(checklist) : undefined,
          assignees: assignees !== undefined ? {
            set: assignees.map((id: string) => ({ id }))
          } : undefined,
        },
      });

      // If column is changing and task has children, move ALL descendants recursively
      if (isColumnChanging && hasChildren) {
        // Recursive function to get all descendant IDs
        const getAllDescendantIds = async (parentTaskId: string): Promise<string[]> => {
          const children = await tx.task.findMany({
            where: { parentId: parentTaskId },
            select: { id: true },
          });

          if (children.length === 0) return [];

          const childIds = children.map(c => c.id);
          const grandchildIds = await Promise.all(
            childIds.map(id => getAllDescendantIds(id))
          );

          return [...childIds, ...grandchildIds.flat()];
        };

        // Get all descendant IDs
        const descendantIds = await getAllDescendantIds(taskId);

        // Update all descendants to the new column
        if (descendantIds.length > 0) {
          await tx.task.updateMany({
            where: { id: { in: descendantIds } },
            data: { columnId },
          });
        }
      }

      return updated;
    });

    // Fetch the updated task with assignees to return complete data
    const taskWithAssignees = await prisma.task.findUnique({
      where: { id: taskId },
      include: {
        assignees: {
          select: {
            id: true,
            name: true,
            email: true,
            image: true,
          },
        },
      },
    });

    return NextResponse.json(taskWithAssignees || updatedTask);
  } catch (error) {
    console.error("Error updating task:", error);
    return NextResponse.json(
      { message: "Error updating task" },
      { status: 500 }
    );
  }
}

// DELETE /api/projects/[projectId]/tasks/[taskId]
export async function DELETE(
  req: NextRequest,
  { params }: { params: Promise<{ projectId: string; taskId: string }> }
) {
  try {
    const session = await auth();
    if (!session) {
      return NextResponse.json({ message: "Unauthorized" }, { status: 401 });
    }

    const { projectId, taskId } = await params;

    // Check permissions
    const userRole = await getUserRoleInProject(session.user.id, projectId);
    if (!hasPermission(userRole, PERMISSIONS.DELETE_TASK)) {
      return NextResponse.json({ message: "Forbidden" }, { status: 403 });
    }

    await prisma.task.delete({
      where: { id: taskId, projectId },
    });

    return NextResponse.json({ message: "Task deleted" }, { status: 200 });
  } catch (error) {
    console.error("Error deleting task:", error);
    return NextResponse.json(
      { message: "Error deleting task" },
      { status: 500 }
    );
  }
}